import React,{Component} from 'react';
import Main from './main';
import Home from './home';
import Navigater from './navigater';
import {Content} from 'react-mdl';
import './App.css';

class App extends Component{
  render(){
    return(
      <div>
        <Navigater />
        <Home />
        <Content>
          <Main />
        </Content>
      </div>
    );
  }
}
export default App;



